.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 1000;
  background: rgba(0, 0, 0, .2);
  transition: opacity .4s cubic-bezier(.4, 0, .2, 1), visibility .4s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.modal {
  width: 100%;
  max-width: 450px;
  max-height: 940px;
  height: 100%;
  background: radial-gradient(circle, #ffefef 12%, #f98e8e 100%);
  border-radius: 4px;
  margin-top: 30px;
  padding: 48px 20px 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal-btn {
  width: 30px;
  height: 30px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
