.backdrop {
    position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.2);
        opacity: 1;
        transition: opacity 400ms cubic-bezier(0.4, 0, 0.2, 1),
            visibility 400ms cubic-bezier(0.4, 0, 0.2, 1);
        z-index: 1000;
}

.is-hidden {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
}

.modal {
    position: absolute;
        top: 50%;
        left: 50%;
        overflow-y: auto;
        width: 100%;
        max-width: 450px;
        max-height: 940px;
        height: 100%;
        margin-top: 30px;
        padding: 48px 20px 40px;
        background: rgb(255, 239, 239);
            background: radial-gradient(circle, rgba(255, 239, 239, 1) 12%, rgba(249, 142, 142, 1) 100%);
        box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
            0px 2px 1px rgba(0, 0, 0, 0.2);
        border-radius: 4px;
        transform: translate(-50%, -50%) scale(1);
        transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-btn {
    position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        top: 8px;
        right: 8px;
        width: 30px;
        height: 30px;
        padding: 0px;
        background-color: transparent;
        border: none;
        cursor: pointer;
}

.modal-btn__icon {
    background-color: transparent;
    cursor: pointer;
}



